// Content.js
import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Main from '../app/Main';
import queryString from 'query-string';

window.qparsed = queryString.parse(window.location.search)

const styles = theme => ({
    layout: {
        overflow: 'hidden'
    },
    buttons: {
        display: 'flex',
        justifyContent: 'flex-end',
    },
    button: {
        marginTop: theme.spacing.unit * 3,
        marginLeft: theme.spacing.unit,
    }
});

class Content extends Component {

    render() {
        const { classes } = this.props;
        
        const debug = window.qparsed.debug === '1' ? ' debug' : '';
        window.showNumbers = (debug && window.qparsed.numbers === '1')
        const numbers = window.showNumbers ? ' numbered' : '';
        
        return (
            <main className={classes.layout + debug + numbers}>
                <Main/>
            </main>
        )
    }
}

export default withStyles(styles)(Content);


