// Cadastro.jsx
import React, { Component } from 'react';
import { Typography } from '@material-ui/core';

// Import { Button } from 'react-bootstrap';
export default class Agradecimento extends Component {
    render() {
        let acumulado = 0;
        return (
            <div id="end">
                <Typography component="h1" variant="h4" align="center">
                    Tarefa concluída!
                </Typography>
                <table border='1'>
                    <tbody>
                        <tr>
                            <th>#</th>
                            <th>Distância (rad)</th>
                            <th>Score</th>
                            <th>Acumulado</th>
                        </tr>
                        {this.props.data.map((e, i) => {
                            acumulado += e.score
                            return (
                                <tr key={i}>
                                    <td>{i + 1}</td>
                                    <td>{e.distancia.toFixed(3)}</td>
                                    <td>{e.score.toFixed(3)}</td>
                                    <td>{acumulado.toFixed(3)}</td>
                                </tr>
                            )
                        })}
                    </tbody>
                </table>
            </div>
        );
    }
}


