// Cadastro.jsx
import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import { Grid } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

const classes = theme => ({
    spaceTop: {
        marginTop: theme.spacing.unit * 2,
    },
    root: {
        justifyContent: 'center'
    }
});

const formDefaults = {
    email: ''
};

class CadastroForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            open: true,
            formData: { ...formDefaults },
            error: {email: ''}
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleClose = this.handleClose.bind(this);
    }

    handleChange = event => {
        let formData = this.state.formData;
        const target = event.target;
        const value = target.value;
        const name = target.name;
        formData[name] = value;

        this.setState({
            'formData': formData
        })
    };

    handleClose = () => {
        if ( this.state.formData.email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) ) {
            this.setState({
                open: false
            });
            this.props.submit(this.state.formData)
        } else {
            this.setState({
                'error': {email: 'Email inválido.'}
            })
        }
    };

    render() {
        const { classes } = this.props;
        return (
            <React.Fragment>
                <Dialog
                    open={this.state.open}
                    aria-labelledby="form-dialog-title"
                >
                    <DialogContent>
                        <Grid container spacing={24}>
                            <Grid item xs={12}>
                                <TextField margin="dense"
                                    name="email"
                                    id="email"
                                    label="Email"
                                    type="text"
                                    onBlur={this.handleChange}
                                    required fullWidth
                                />
                                {(this.state.error.email.length > 0) && (
                                    <div style={{color:'red'}}>{this.state.error.email}</div>
                                )}
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <DialogActions classes={{root: classes.root}}>
                        <Button variant="outlined" onClick={this.handleClose} color="primary">
                            Iniciar
                        </Button>
                    </DialogActions>
                </Dialog>
            </React.Fragment>
        );
    }
}

export default withStyles(classes)(CadastroForm);


